.search-bar {
  margin-top: 10px !important;
}

.header {
  font-size: 15px;
  text-transform: uppercase;
}

.logo {
  width: 120px;
  margin: 15px auto;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.img {
  border-radius: 0.28em;
}

.btn {
  position: absolute;
  right: 0;
  height: inherit;
}

.label {
  width: 12%;
  margin-bottom: 10px !important;
}

.video {
  border-radius: 0.28rem;
}

.video-hover:hover {
  transform: scale(1.1);
  border-radius: 0.28em;
  background-color: #f1f1f1;
  box-shadow: 0 1.8px 2.2px rgba(0, 0, 0, 0.034), 0 6.7px 5.3px rgba(0, 0, 0, 0.048), 0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072), 0 41.8px 33.4px rgba(0, 0, 0, 0.086), 0 100px 80px rgba(0, 0, 0, 0.12);

  margin: 5px !important;
}

.video-hover {
  transition: all 0.2s ease-out;
}
